





























import { Component, Prop, Vue } from 'vue-property-decorator';

@Component
export default class LastWord extends Vue {
  message = '';
  isSent = false;

  sendMessage() {
    if (!this.message) {
      return;
    }
    this.$socket.werewolves.emit('sendlastword', { message: this.message });
    this.isSent = true;
  }

  clearMessage() {
    this.message = '';
  }
}
